.contact__form {
  width: 100%;
  margin: 50px auto;
  max-width: 800px;
}
.contact__input {
  width: 100%;
  box-sizing: border-box;
  color: #93939f;
  font-size: 1.2rem;
  background-color: transparent;
  border: 3px solid #6caa07;
  padding: 20px 10px;
  border-radius: 4px;
  margin-bottom: 30px;
  outline: none;
}
::placeholder {
  color: #93939f;
}
.contact__message {
  margin: 20px 30px;
  padding: 20px;
  font-size: 2rem;
  font-family: cursive;
  border-radius: 4px;
  display: inline-block;
}
.contact__message--success {
  border: 2px solid #6caa07;
  color: #93939f;
}
.contact__message--error {
  border: 2px solid #ff4d4d;
  color: #ff4d4d;
}
textarea.contact__input {
  resize: none;
  font-family: sans-serif;
  font-size: 1.2rem;
}
.contact__button {
  color: #0c0c0c;
  border: 1px solid #0a0a0a;
  padding: 6px 16px;
  font-size: 0.875rem;
  min-width: 64px;
  box-sizing: border-box;
  border-radius: 4px;
  font-size: 1.4rem;
  font-weight: 400;
  text-decoration: none;
  max-width: 170px;
  text-align: center;
  display: inline-block;
  margin: 50px 0 20px 0;
  transition: 0.3s;
  background-color: #8bbd18;
}
.contact__button:hover {
  background: rgba(238, 238, 238, 0.603);
}

@media screen and (max-width: 600px) {
  .contact__input {
    font-size: 1rem;
  }

  .contact__message {
    margin: 20px 0px;
    padding: 10px;
    font-size: 1.5rem;
  }
}
