@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: rgb(248, 242, 242);
  background-color: #141434;
}

.loading-container {
  width: 600px;
  height: 600px;
  margin-top: 10%;
  margin-left: 30%;
}

@media screen and (max-width: 1100px) {
  .loading-container {
    margin-left: 10%;
    margin-top: 15%;
    width: 600px;
    height: 600px;
  }
}
@media screen and (max-width: 600px) {
  .loading-container {
    margin-left: 5%;
    margin-top: 30%;
    width: 310px;
    height: 350px;
  }
}

.navbar {
  position: sticky;
  top: 0;
  box-shadow: 0 2px 2px rgba(14, 0, 65, 0.1);
  z-index: 10000;
  background-color: #091c34;
  padding: 10px 0;
  font-family: cursive;
}
.navbar__main {
  display: flex;
  max-width: 1200px;
  margin: 8px 20px;
  justify-content: space-between;
  align-items: center;
  background-color: #091c34;
}
.navname {
  width: 60px;
  margin: 0 20px;
  color: #fefeff;
  background-color: #091c34;
}
.navbar__list--desktop {
  list-style: none;
  display: flex;
  margin: 0;
  background-color: #091c34;
}
.navbar__list--desktop li {
  margin: 0 20px;
  font-size: 1.2rem;
  background-color: #091c34;
}
.navbar__list--desktop li a {
  color: #fefeff;
  text-decoration: none;
  background-color: #091c34;
  font-family: cursive;
}
.navbar__list--desktop li::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.25rem;
  margin-top: 0.25rem;
  border-radius: 0.1875rem;
  background: #8bbd18;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: 0.17s;
}
.navbar__list--desktop li:hover a {
  transition: 0.17s;
}
.navbar__list--desktop li:hover a {
  color: #7ebd18;
}
.navbar__list--desktop li:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.navbar__list--mobile {
  display: none;
}
.navbar__mobile--icon {
  display: none;
}
@media screen and (max-width: 650px) {
  .navbar__list--desktop {
    display: none;
  }
  .navbar__mobile--icon {
    color: #ffffff;
    display: inline;
    font-size: 2.5rem;
    cursor: pointer;
    margin: 0 5px;
  }
  .navbar__list--mobile {
    display: block;
    margin: 0;
    padding: 0;
    height: 0;
    list-style: none;
    position: absolute;
    top: 100%;
    left: 0;
    font-size: 1.2rem;
    z-index: 1000;
    background-color: #000052;
    transition: 0.2s;
    width: 100%;
    overflow: hidden;
  }
  .navbar__list--mobileShow {
    height: 180px;
  }
  .navbar__list--mobile li a {
    display: flex;
    color: #ffffff;
    justify-content: center;
    align-items: center;
    height: 60px;
    text-decoration: none;
  }

  .navbar__list--mobile li a:hover {
    background: #639703;
  }
}

.intro {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  margin: 0 auto;
  padding: 120px 0 100px 0;
  width: 90%;
  max-width: 1100px;
}

.intro__image {
  position: relative;
  width: 400px;
  height: 420px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  padding: 0px 60px 0px 0px;
}

.details p {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #93939f;
  padding: 15px 0 10px 0;
}

.intro__desc {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  width: 650px;
  margin-left: 70px;
}

.intro__head {
  color: #aeaeb7;
  font-size: 3.3rem;
  margin: 10px 0;
  font-weight: 600;
  font-family: cursive;
}
.intro__text {
  margin: 0;
  font-size: 1.7rem;
  color: #c7c7cc;
  font-weight: 600;
}
.intro__aboutLink {
  color: #0c0c0c;
  border: 1px solid #0a0a0a;
  padding: 6px 16px;
  font-size: 0.875rem;
  min-width: 64px;
  box-sizing: border-box;
  border-radius: 4px;
  font-size: 1.4rem;
  font-weight: 400;
  text-decoration: none;
  max-width: 170px;
  text-align: center;
  display: inline-block;
  margin: 50px 0 20px 0;
  transition: 0.3s;
  background-color: #8bbd18;
}
.intro__aboutLink:hover {
  background: rgba(238, 238, 238, 0.603);
}
@media screen and (max-width: 1100px) {
  .intro {
    grid-template-columns: 100%;
    padding: 50px 0 100px 0;
  }
  .intro__image {
    margin: 0 auto;
    order: 2;
  }
  .intro__desc {
    box-sizing: border-box;
    width: 100%;
    order: 1;
  }
}

@media screen and (max-width: 600px) {
  .intro__image {
    width: 290px;
    height: 290px;
    padding: 0px 0px;
  }

  .intro__desc {
    margin: 0;
  }
  .intro__head {
    font-size: 2.4rem;
  }
  .intro__text {
    font-size: 1.5rem;
  }
  .intro__aboutLink {
    font-size: 1.2rem;
  }
}

.about-container {
  width: 400px;
  height: 500px;
}

@media screen and (max-width: 1100px) {
  .about-container {
    margin-left: 20%;
  }
}
@media screen and (max-width: 600px) {
  .about-container {
    margin-left: 5%;
    width: 310px;
    height: 350px;
  }
}

.project {
  color: white;
  width: 100%;
  box-sizing: border-box;
  margin: 80px 0;
  display: flex;
}
.project__basicInfo {
  box-sizing: border-box;
  width: 40%;
  border: 3px solid #6caa07;
  display: flex;
  flex-direction: column;
}
.project__detail {
  box-sizing: border-box;
  width: 60%;
  padding: 0 20px 0 50px;
  border-radius: 4px;
  align-items: center;
}
.project__title {
  color: #aeaeb7;
  font-size: 2.5rem;
  margin: 0px 10px 15px 10px;
  font-family: cursive;
}
.project__langs {
  display: flex;
  flex-wrap: wrap;
}
.project__lang {
  border: 2px solid #93939f;
  color: #93939f;
  padding: 5px 10px;
  border-radius: 10px;
  margin: 10px;
}
.project__desc {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #93939f;
  padding: 0 0 10px 0;
}
.project__links {
  margin: 20px 0 10px 0;
}
.project__links a {
  font-size: 2rem;
  margin: 0 10px;
  color: #18babd;
}

.project__image {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

@media screen and (max-width: 1000px) {
  .project {
    flex-direction: column;
    margin: 50px 0;
  }
  .project__basicInfo {
    width: 100%;
  }
  .project__detail {
    width: 100%;
    padding: 15px;
  }
}

@media screen and (max-width: 600px) {
  .project__title {
    font-size: 1.5rem;
  }
  .project__desc {
    font-size: 1.1rem;
  }
}

/* Calling projects component */
.projects {
  width: 90%;
  max-width: 1200px;
  margin: 100px auto;
}
.projects__projectCont {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (max-width: 600px) {
  .projects {
    margin: 20px auto;
  }
}

.section__heading {
  margin: 0 auto;
  padding: 0px 500px 100px 400px;
  font-size: 5rem;
  font-weight: 600;
  font-family: emoji;
}

@media screen and (max-width: 1100px) {
  .section__heading {
    padding: 0 100px 50px 150px;
  }
}

@media screen and (max-width: 600px) {
  .section__heading {
    padding: 0 10px 50px 20px;
  }
}

.skill {
  grid-template-columns: auto auto;
  margin: 0 auto;
  padding: 80px 0 100px 0;
  width: 90%;
  max-width: 1100px;
}

.skill__heading {
  margin: 0 auto;
  padding: 50px 200px 100px 300px;
  font-size: 5rem;
  font-weight: 600;
  font-family: emoji;
}

.skill__section {
  color: white;
  width: 100%;
  box-sizing: border-box;
  margin: 80px auto;
  display: flex;
  padding: 0px 20px 0px 20px;
}

.skill__box {
  padding: 50px;
  width: 20%;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  text-align: center;
}

.skill__image {
  position: relative;
  width: 105px;
  height: 105px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

@media screen and (max-width: 1100px) {
  .skill__heading {
    padding: 0 50px 0px 100px;
  }

  .skill {
    grid-template-columns: 100%;
    padding: 50px 0 100px 0;
  }

  .skill__section {
    padding: 0px 0px;
  }
  .skill__box {
    width: 20%;
    padding: 20px;
    text-align: left;
  }

  .skill__image {
    width: 80px;
    height: 80px;
    display: flex;
  }
}

@media screen and (max-width: 600px) {
  .skill__heading {
    padding: 0 30px 0px 50px;
    font-size: 3rem;
  }

  .skill__look {
    display: flex;
    flex-direction: row;
  }
  .skill__section {
    flex-direction: column;
    margin: 50px 0;
    padding: 0px 0px;
  }

  .skill__box {
    width: 100%;
    padding: 40px 0px 40px 20px;
  }
}

.contact {
  width: 90%;
  max-width: 1200px;
  margin: 80px auto;
}

.contact__heading {
  margin: 0 auto;
  padding: 50px 100px 100px 450px;
  font-size: 5rem;
  font-weight: 600;
  font-family: emoji;
}

.contact__section {
  color: white;
  width: 100%;
  box-sizing: border-box;
  margin: 80px 0;
  display: flex;
}

.contact__info {
  box-sizing: border-box;
  padding: 20px 50px 20px 50px;
  width: 40%;
  border: 3px solid #6caa07;
  display: flex;
  flex-direction: column;
}

.contact__title {
  color: #aeaeb7;
  font-size: 1.8rem;
  margin: 15px 0;
}

.contact__text {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #93939f;
}

.contact__social {
  padding: 0;
  list-style: none;
  display: flex;
  margin: 20px 0 40px 0;
  flex-direction: column;
}
.contact__social a {
  font-size: 22px;
  border: 2px solid #93939f;
  padding: 10px;
  color: #aeaeb7;
  cursor: pointer;
  margin: 10px 20px 0 20px;
  border-radius: 15px;
  transition: 0.25s;
  text-decoration: none;
}
.contact__social a:hover {
  color: #aad60a;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.form__detail {
  width: 60%;
  padding: 0 50px;
  border-radius: 4px;
  align-items: center;
}

@media screen and (max-width: 1000px) {
  .contact__section {
    flex-direction: column;
    margin: 50px 0 0 0;
  }

  .contact__heading {
    padding: 0 100px 50px 150px;
  }

  .contact__info {
    width: 100%;
    padding: 15px;
  }

  .form__detail {
    width: 100%;
    padding: 15px;
  }
}

@media screen and (max-width: 600px) {
  .contact {
    margin: 60px auto;
  }
  .contact__heading {
    padding: 0 10px 50px 20px;
  }
  .contact__title {
    font-size: 1.5rem;
  }
  .contact__text {
    font-size: 1.1rem;
  }
  .form__detail {
    width: 100%;
    padding: 15px 0 15px 5px;
  }
}

.contact__form {
  width: 100%;
  margin: 50px auto;
  max-width: 800px;
}
.contact__input {
  width: 100%;
  box-sizing: border-box;
  color: #93939f;
  font-size: 1.2rem;
  background-color: transparent;
  border: 3px solid #6caa07;
  padding: 20px 10px;
  border-radius: 4px;
  margin-bottom: 30px;
  outline: none;
}
::-webkit-input-placeholder {
  color: #93939f;
}
:-ms-input-placeholder {
  color: #93939f;
}
::placeholder {
  color: #93939f;
}
.contact__message {
  margin: 20px 30px;
  padding: 20px;
  font-size: 2rem;
  font-family: cursive;
  border-radius: 4px;
  display: inline-block;
}
.contact__message--success {
  border: 2px solid #6caa07;
  color: #93939f;
}
.contact__message--error {
  border: 2px solid #ff4d4d;
  color: #ff4d4d;
}
textarea.contact__input {
  resize: none;
  font-family: sans-serif;
  font-size: 1.2rem;
}
.contact__button {
  color: #0c0c0c;
  border: 1px solid #0a0a0a;
  padding: 6px 16px;
  font-size: 0.875rem;
  min-width: 64px;
  box-sizing: border-box;
  border-radius: 4px;
  font-size: 1.4rem;
  font-weight: 400;
  text-decoration: none;
  max-width: 170px;
  text-align: center;
  display: inline-block;
  margin: 50px 0 20px 0;
  transition: 0.3s;
  background-color: #8bbd18;
}
.contact__button:hover {
  background: rgba(238, 238, 238, 0.603);
}

@media screen and (max-width: 600px) {
  .contact__input {
    font-size: 1rem;
  }

  .contact__message {
    margin: 20px 0px;
    padding: 10px;
    font-size: 1.5rem;
  }
}

.fotter {
  margin: 0px auto 20px auto;
  padding: 10px;
  position: relative;
  width: 90%;
  max-width: 1200px;
}

.fotter__head {
  color: #aeaeb7;
  font-size: 1.5rem;
  text-align: center;
  font-weight: 400;
  margin-top: 50px;
  font-family: cursive;
}

@media screen and (max-width: 600px) {
  .fotter__head {
    font-size: 1.2rem;
  }
}

.bottom-container {
  width: 600px;
  height: 400px;
  margin-left: 25%;
}

@media screen and (max-width: 1100px) {
  .bottom-container {
    margin-left: 10%;
    width: 400px;
  }
}
@media screen and (max-width: 600px) {
  .bottom-container {
    margin-left: 10%;
    width: 300px;
    height: 400px;
  }
}

